@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

@layer base {
  body {
    @apply bg-light dark:bg-primary text-primary dark:text-light;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

@layer components {
  .btn-primary {
    @apply bg-primary text-light dark:bg-accent dark:text-primary px-4 py-2 rounded-md font-medium transition-all hover:opacity-90;
  }
  
  .btn-secondary {
    @apply bg-transparent border border-primary text-primary dark:border-accent dark:text-accent px-4 py-2 rounded-md font-medium transition-all hover:bg-primary hover:text-light dark:hover:bg-accent dark:hover:text-primary;
  }
  
  .card {
    @apply bg-white dark:bg-secondary rounded-lg shadow-md p-6;
  }
  
  .input {
    @apply w-full px-4 py-2 border border-gray-300 dark:border-tertiary rounded-md focus:outline-none focus:ring-2 focus:ring-accent dark:bg-tertiary dark:text-light;
  }
}
